@import url("https://fonts.googleapis.com/css?family=Abril+Fatface&display=swap");

.body {
  height: 100vh;
  max-width: 400px;
  width: 100vw;
  margin: auto;
}
.main {
  height: 90%;
  display: flex;
  justify-content: center;
  margin: auto;
  align-items: center;
  flex-direction: column;
}

.footer {
  display: flex;
  height: 10%;
  background-color: #489fb0;
  color: #fff;
  align-items: center;
  justify-content: center;
  font-family: "Abril Fatface";
  width: 100%;
}
.input {
  width: 250px;
  height: 35px;
  border: none;
  border-radius: 20px;
  padding: 0 20px;
  color: rgb(18, 53, 209);
  border: #738f93 1px solid;
  letter-spacing: 1px;
}
.input:focus {
  outline: none;
}
.btn {
  margin-top: 20px;
  min-width: 150px;
}
.label {
  font-size: 1.6em;
  color: #489fb0;
}
